.speakers {

  &__content {
    @include onDesktop {
      @include useGrid (12, 24px);
    }
  }
}

.speaker {


  &__image-container {
    // @include useGrid (2, 20px);
    margin-bottom: 32px;


    @include onTablet {
      @include useGrid (6, 24px);

    }

    @include onDesktop {
      @include useGrid (12, 24px);

    }
  }

  &__image {
    grid-column: 1 / 2;
    width: 100%;


    @include onTablet {

    }

    @include onDesktop {

    }
  }

  &__subtittle {
    margin-top: 0;
    font-weight: 700;
    font-size: 16px;
